<!-- 进度组件 -->
<template>
	<view class="showmodel" v-if="flag" style="width: 100%;">
		<view class="" style="height: 30vh;"></view>
		<view class="" style="margin: 0 60rpx; ">
			<view class="b-r-30" style=" position: relative; ">	
				<image @click.stop="closeLoad" src="../../static/fristPage/X.png" mode="widthFix" 
				style="position: absolute; top: 15rpx; right: 15rpx; width: 30rpx;"></image>
				<!-- 进度显示 -->
				<view class="">
					<view class="b-b-r-l-t-15rpx ta-c df-jcc-aic fw800 color-Oeb5d2d"
					style="height: 140rpx; background-color: #fcfcfc;  padding: 0 30rpx;">
						<view style="white-space: pre-line">Loading...</view>
					</view> 
					<view class="b-b-r-l-r-15rpx" style="height: 140rpx; background-color: #FFF;">
						<view class="" style="height: 45rpx;"></view>
						<view class="" style="height: 40rpx;">
							<view class="" style="margin: 0 50rpx;  border-radius: 20rpx; overflow: hidden;">
								<progress percent="" :percent="percent" border-radius="20rpx" stroke-width="20"
								activeColor="#0cc0c0"  />
							</view>
						</view>
					</view>
				</view> 	
			</view>
		</view>
		
	</view>
</template>

<script>
    export default{
		data(){
			return{
				flag:false,
				percent:2
			}
		},
		methods:{
			closeLoad(){
				this.flag = false 
				this.percent = 0
				clearTimeout(this.timer)
			},
			openLoad(){
				this.addPercent()
				this.flag = true
				this.percent = 0
			},
			addPercent(){
				var that = this;
				if(that.percent >= 0 && that.percent <= 100){
					that.percent = that.percent + 0.3 
				}else{
					that.percent = 0
				} 
				var timer = setTimeout(function(){
					that.addPercent()  
				},50);   
			},


			 
		}
	}
</script>

<style>
	.showmodel{
		z-index: 20;
		background: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		right: 0;
		bottom: -100rpx; 
		top: 0;
	}
</style>
